import React, { useMemo } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import { Container, Form, Grid, Input, Label } from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import database from './database.json';
import replacements from './replacements.json';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const DATABASE = {
  dictionary: {
    array: database.dictionary,
    set: new Set(database.dictionary),
  },
  associations: database.associations,
  replacements,
};

const getReplacementsForWord = (word) => {
  return DATABASE.replacements.reduce((result, words) => {
    if (words.includes(word)) {
      return [...result, ...words.filter((item) => item !== word)];
    }

    return result;
  }, []);
};

const App = () => {
  const [scheme, handleSchemeChange] = useField('');
  const [mask, handleMaskChange] = useField('');

  const [schemeDebounced] = useDebounce(scheme, 500);
  const [maskDebounced] = useDebounce(mask, 500);

  const schemeClean = useMemo(() => schemeDebounced.trim().toLowerCase().replace('—ë', '–µ'), [
    schemeDebounced,
  ]);

  const maskClean = useMemo(() => maskDebounced.trim().toLowerCase().replace('—ë', '–µ'), [
    maskDebounced,
  ]);

  const words = useMemo(() => {
    const schemeParts = schemeClean.split(/\s+/);
    if (schemeParts.length < 2) {
      return [];
    }

    const regexParts = [];

    const isValid = schemeParts.every((schemePart) => {
      if (schemePart) {
        if (/^[–∞-—è]+!$/.test(schemePart)) {
          regexParts.push([`${schemePart.slice(0, -1)}`, null]);
        } else if (/^\.+$/.test(schemePart)) {
          regexParts.push([`[–∞-—è]{${schemePart.length}}`, null]);
        } else if (schemePart === '*') {
          regexParts.push(['[–∞-—è]+', null]);
        } else if (schemePart === '?') {
          regexParts.push(['[–∞-—è]+', DATABASE.dictionary.set]);
        } else if (/^(([–∞-—è]+,)*[–∞-—è]+$)/.test(schemePart)) {
          if (schemePart.includes(',')) {
            regexParts.push([`${schemePart.split(',').join('|')}`, null]);
          } else {
            regexParts.push([
              [
                ...(schemePart.length > 1 ? [schemePart.split('').reverse().join('')] : []),
                ...getReplacementsForWord(schemePart),
                ...(DATABASE.associations[schemePart] || []),
              ].join('|'),
              null,
            ]);
          }
        } else {
          return false;
        }
      }

      return true;
    });

    if (!isValid) {
      return [];
    }

    const regex = new RegExp(`^${regexParts.map(([regexPart]) => `(${regexPart})`).join('')}$`);

    let maskRegex = null;
    if (maskClean) {
      try {
        maskRegex = new RegExp(maskClean);
      } catch {}
    }

    return DATABASE.dictionary.array.reduce((result, word) => {
      const match = word.match(regex);
      if (!match) {
        return result;
      }

      if (maskRegex && !maskRegex.test(word)) {
        return result;
      }

      const isValid = regexParts.every(([_, dictionarySet], index) => {
        if (!dictionarySet) {
          return true;
        }

        return dictionarySet.has(match[index + 1]);
      });

      if (isValid) {
        result.push(match.slice(1).join(' '));
      }

      return result;
    }, []);
  }, [schemeClean, maskClean]);

  return (
    <>
      <div className={styles.header}>
        <Container>
          <Grid padded stackable>
            <Grid.Row>
              <Grid.Column width={8}>
                <Form>
                  <Input
                    fluid
                    autoFocus
                    value={scheme}
                    placeholder="–°—Ö–µ–º–∞"
                    size="big"
                    onChange={handleSchemeChange}
                  />
                </Form>
              </Grid.Column>
              <Grid.Column width={8}>
                <Form>
                  <Input
                    fluid
                    value={mask}
                    placeholder="–ú–∞—Å–∫–∞"
                    size="big"
                    onChange={handleMaskChange}
                  />
                </Form>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </div>
      <div className={styles.content}>
        <Container>
          <Grid padded>
            <Grid.Row>
              <Grid.Column>
                <div className={styles.words}>
                  {words.map((word, i) => (
                    <Label
                      horizontal
                      key={word}
                      className={classNames(styles.word, i % 2 && styles.odd)}
                    >
                      {word}
                    </Label>
                  ))}
                </div>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </div>
    </>
  );
};

export default withAuth(App);
