import React from 'react';
import ReactDOM from 'react-dom';

import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import App from './App';

const registerString = new URLSearchParams(window.location.search).get('register');
const registerStringPrev = localStorage.getItem('registered');

let registerMethod;
if (['true', 'false'].includes(registerString)) {
    const register = registerString === 'true';
    registerMethod = register ? 'register' : 'unregister';

    if (registerString !== registerStringPrev) {
        localStorage.setItem('registered', register);
    }
} else {
    registerMethod = registerStringPrev === 'true' ? 'register' : 'unregister';
}

ReactDOM.render(<App />, document.getElementById('root'));

serviceWorkerRegistration[registerMethod]();
